.overlay_effect {
	position: absolute;
	top: 0px;
	bottom: 0px;
	left: 0px;
	right: 0px;
	z-index: 3;
}
.overlay_effect:after {
	position: absolute;
	content: "";
	top: 0px;
	bottom: 0px;
	left: 0px;
	right: 0px;
	background-color: #fff;
	z-index: 1;
}
.overlay_effect:before {
	position: absolute;
	content: "";
	top: 0px;
	bottom: 0px;
	left: 0px;
	right: 0px;
}
body.loaded .overlay_effect:before {
	z-index: 2;
	background-color: #000;
	animation: anim 2s cubic-bezier(0.77, 0, 0.175, 1) both;
}

body.loaded .overlay_effect:after {
	animation: anim2 2s cubic-bezier(0.77, 0, 0.175, 1) both;
}

@keyframes anim {
	0% {
		transform: translateX(-100%);
	}
	50% {
		transform: translateX(0);
	}
	100% {
		transform: translateX(100%);
	}
}

@keyframes anim2 {
	0% {
		transform: translateX(0);
	}
	100% {
		transform: translateX(100%);
	}
}
.shane_tm_down {
	position: absolute;
	z-index: 7;
	bottom: 50px;
	display: none;
}
.shane_tm_down.loaded {
	display: block;
	@media screen and (max-width: 767px) {
		display: none;
	}
}
.shane_tm_down[data-position="left"] {
	left: 0px;
}
.shane_tm_down[data-position="right"] {
	right: 0px;
}
.shane_tm_down[data-position="center"] {
	left: 50%;
	transform: translateX(-50%);
}
.shane_tm_down .line_wrapper {
	position: absolute;
	width: 1px;
	height: 100%;
	left: 0;
	right: 0;
	margin: 0px auto;
	opacity: 1;
	filter: alpha(opacity=100);
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
	visibility: visible;

	-webkit-transition: all 0.7s;
	-moz-transition: all 0.7s;
	-ms-transition: all 0.7s;
	-o-transition: all 0.7s;
	transition: all 0.7s;
}
.shane_tm_down .line_wrapper {
	width: 1px;
	height: 50px;
	background: none;
	display: block;
}
.shane_tm_down .line_wrapper:before {
	content: "";
	background-color: #000;
	width: 1px;
	height: 50%;
	display: block;
	top: 0;
	animation: scroll_down 2s ease-in-out infinite;
	@media screen and (max-width: 767px) {
		background-color: #fff;
	}
}
@keyframes scroll_down {
	0% {
		transform: translate(0, -20px);
		opacity: 0;
		height: 0;
	}
	50% {
		opacity: 1;
		height: 50%;
	}
	100% {
		transform: translate(0, 20px);
		opacity: 0;
	}
}

.shane_tm_down[data-skin="light"] .line_wrapper:before {
	background-color: #fff;
}

